import React,{Component} from 'react';
import '../assets/style.css';
import QuizService from '../quizService/index.js';
import Questions from './Questions.js';
import Result from '../component/Result.js';


class Quiz extends Component {
    state = {
        question : [],
        error : null,
        answer : [],
        correct:[],
        score: 0,
        resultPage:false
    }
    getQuiz = () => {
        QuizService().then(response =>{
            console.log(response)
            this.setState({
                question : response
            })
        })
        .catch (resolve => {
            console.log(resolve)
            this.setState({
                error : resolve
            })
        })
    }
    componentDidMount() {
        this.getQuiz();
    }
    setScore = ()=>{
        let answer = this.state.answer;
        let correct = this.state.correct;
            for (let i in answer){
                for (let j in correct){
                    
                    if (answer[i].questionNo === correct[j].questionNo){
                       // console.log(answer[i].answer,'===',correct[j].correctAnswer,' && ',answer[i].questionNo, '===', correct[j].questionNo)
                        if (answer[i].answer === correct[j].correctAnswer){
                            this.setState(prevState => ({
                                score : prevState.score + 1
                            }))
                            //console.log(this.state.score)
                        }
                    }
                }
            }
            this.setState({resultPage:true});   
    }
    setAnswer = (ans,i) => {
        let answer = this.state.answer.map(elem => {
            return elem.answer
        })
        //console.log("setanswer ",answer);
        if (answer.indexOf(ans) === -1) {
            this.setState({
                answer:[...this.state.answer,{answer:ans,questionNo:i}]
            })
        }
       
    }
    setCorrect = (correctAns,i) => {
        let correct = this.state.correct.map(elem => {
            return elem.correctAnswer
        })
        //console.log("setanswer ",answer);
        if (correct.indexOf(correctAns) === -1) {
            this.setState({
                correct:[...this.state.correct,{correctAnswer:correctAns,questionNo:i}]
            })
        }
    }
    playAgain = () => {
        this.setState({
            question : [],
            error : null,
            answer : [],
            correct:[],
            score: 0,
            resultPage:false
        });
        this.getQuiz();
    }
    
    render(){
       
        const questions = this.state.question.map((ques,index) => {
            return (<Questions question={ques.question}
            answer={ques.answers}
            correct={ques.correct}
            key={ques.questionId}
            QuesNo={index+1}
            setAnswer={(ans)=>this.setAnswer(ans,index+1)}
            setCorrect={()=>this.setCorrect(ques.correct,index+1)}/>);
           
        })
        let submitBtn = null;
        if (!this.state.resultPage){
            if (this.state.answer.length === 5) {
                submitBtn =  <button className='answerBtn' onClick={this.setScore} autoFocus>Submit</button>
            }
        }
        return (
            <div className='container'>
                <h1 className='title'>QuizMania</h1>
                {this.state.resultPage ?
                <Result score={this.state.score} playAgain={this.playAgain}/>
                :questions}
                {submitBtn}
            </div>
        )
    }
}

export default Quiz;