import React,{Component} from 'react';
import '../assets/style.css';

class Questions extends Component{
    state = {
        answers:[],
        storeAnswer:[],
    }
    componentDidMount(){
        this.setState({
            answers:this.props.answer
        });
    }
    
    answerHandler = (answerName) => {
        // let answerArray = [
        //     ...this.state.storeAnswer,
        //     {answer:answerName}
        // ];
        //console.log(answerArray)
        this.setState({
            answers : [answerName],
            
        });
    }
    render(){
        const answers = this.state.answers.map((ans,index) => {
            return <button onClick={()=>{this.answerHandler(ans);this.props.setAnswer(ans);this.props.setCorrect()}} className='answerBtn' key={index}>{ans}</button>
        });
        return (
            <div className='questionBox'>
                <h1 className='question'>{this.props.QuesNo+'.  '+this.props.question}</h1>
                {answers}
            </div>
        )
    }
}

export default Questions;